body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primery-dark-color: #000;
  --primary-light-color: #00acdc;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  display: block;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item {
  text-transform: uppercase;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
}

.nav-item:first-child {
  margin-left: 0;
}

.nav-link {
  color: var(--primery-dark-color);
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.is-active {
  color: var(--primary-light-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--primary-light-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav-link:hover, a[data-qa="hover"]:hover {
  color: var(--primary-light-color);
}
